<template>
    <div class="posts">
        <div 
          class="post-card"
          v-for="post in posts"
          :key="post.id">
            <div class="img-div">
                <img src="@/assets/logo.png" alt="Post Image">
            </div>
            <div class="post-info">
                <div class="post-title">{{ post.title }}</div>
                <div class="post-author">by {{ post.owner }}</div>
                <!-- <div class="post-time">{{post.date}}</div> -->
                <div class="post-body">{{ truncateText(post.body, 25) }}</div>
                <router-link :to="`/post/${post.id}`"><button class="read-more-link">Read More!</button></router-link>
            </div>
        </div>
    </div>
</template>

<script>
// import json from "@/data/posts.json"
import axios from "axios"

export default {
    name: "Posts",
    data() {
        return {
            posts: []
        }
    },
    methods: {
        async getPosts() {
            const res = await axios.get(`${this.$store.state.baseUrl}api/posts`)
            const json = res.data.results;
            this.posts = json;
        },
        truncateText(text, words) {
            return text.split(" ").splice(0, words).join(" ");
        }
    },
    mounted() {
        this.getPosts();
        console.log(this.posts);
    }
}
</script>

<style lang="css" scoped>
.posts {
    @apply w-full flex flex-col;
}

.post-card {
    @apply flex w-11/12 mx-auto my-4 transition-all transform hover:shadow-2xl hover:scale-105;
}

.img-div {
    @apply w-2/5 mx-2;
}

.post-info {
    @apply mx-2;
}

.post-title {
    @apply text-2xl font-extrabold;
}

.post-author {
    @apply italic font-extrabold;
    color: #999999;
}

.post-time {
    @apply italic;
    color: #999999;
}

.post-body {
    @apply text-xl;
}

.read-more-link {
    @apply bg-green-400 border-none rounded-md text-gray-100 text-xl p-2 m-2 cursor-pointer transition-all duration-500 hover:bg-green-700;
}
</style>